/*
    positioning
*/

.make-relative {
    position: relative;
}

.make-absolute {
    position: absolute;
}

/*
    flex
*/
.flex-center {
    display: flex !important;
    align-items: center;
    justify-content: center;
}

.flex-col {
    display: flex !important;
    flex-direction: column;
}


.hidden {
    display: none !important;
}

/*
 *    SIZING
 */

.full {
    width: 100%;
}

.make-full-screen {
    width: 100vw;
    height: 100vh;
}

.make-full-size {
    width: 100%;
    height: 100%;
}

.inline-section {
    position: relative;
    display: inline-block;
    margin-right: 20px;
}

.section-inline p span{
    font-size: 30px;
    line-height: 1.1;
}

.half-width {
    width: 50%;
}

.full-width {
    width: 100%;
}

/* INPUT SIZING CLASSES */

.small-input {
    width: 50%;
}

.large-input {
    width: 80%;
}


/*
 *  Z-INDEXING
 */

.focus-elements {
    z-index: 2;
}

.focus-navbar-forground {
    position: relative;
    z-index: 6;
}